/* Prevent the prompt from going too wide */
shiny-chat-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
shiny-chat-container shiny-chat-messages {
  overflow: auto;
}
shiny-chat-container .message-container {
  display: flex;
  margin-bottom: 1rem;
  gap: 1rem;
  /* Make badges more circular */
}
shiny-chat-container .message-container .badge {
  --bs-badge-padding-y: 0.65em;
  height: fit-content;
}
shiny-chat-container .message-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
shiny-chat-container .message-content p:last-child {
  margin-bottom: 0;
}
shiny-chat-container .message-content pre code {
  /* Fix alignment of first line in a code block */
  padding: 0;
}
shiny-chat-container shiny-chat-input {
  margin-top: auto;
  position: sticky;
  bottom: 0;
  padding: 0.25rem;
  background-color: var(--bs-body-bg, white);
}